.rect {
  width: 600px;
  border: 2px solid #ffffff;
  border-radius: 20px;
  padding: 20px;
  background-color: transparent;
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 20px;
  border:1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
  
}

.goals-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.goals-container > ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center; 
}

.goal-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  border-radius: 30px;
  margin: 10px 0;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
  width: 450px; 
}

.icons {
  display: flex;
}

.edit-icon, .delete-icon {
  cursor: pointer;
  margin: 0 5px;
}

.goals-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; 
}

h1 {
  color: #ffffff;
}

.add-goals {
  background-image: linear-gradient(to right, #5ca9fb 0%, #6372ff 100%);
  border: none;
  padding: 10px 30px;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-top: 20px;
}

.plus-symbol {
  color: white;
  font-size: 24px;
}

@media screen and (max-width: 540px) {
  .view {
      width: 400px;
  }
}

@media screen and (min-width: 540px) and (max-width: 780px) {
  .view {
      width: 600px;
  }
}