body {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  background-image: url(./img/v3.jpg);
}

.app-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  overflow: auto;
}

.navbar-open .main-content,
.navbar-collapsed .main-content {
  transition: margin-left 0.3s ease, margin-right 0.3s ease;
}

.navbar-open .main-content {
  margin-left: 200px; 
}

.navbar-collapsed .main-content {
  margin-left: 60px; 
}

.main-content {
  padding: 20px;
  width: 100%;
  max-width: 1200px; 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; 
  margin-left: auto; 
  margin-right: auto; 
  transition: margin-left 0.3s ease;
}

.goal-section,
.stopwatch-section,
.calendar-section,
.chart-section {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center;
  margin-bottom: 40px; 
}

.calendar-section {
  max-width: 900px;
  margin-bottom: 60px;
}

.switch-view-button {
  margin: 20px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

.App-link {
  color: #61dafb;
}

@media screen and (max-width: 540px) {
  .view {
      width: 400px;
  }
}

@media screen and (min-width: 540px) and (max-width: 780px) {
  .view {
      width: 600px;
  }
}